$bgcolor: #202023e5;
$fgcolor: #e2e2e2;
$actcolor: #8fb6ff;

.SettingsWindow {

	img {display:block}

    .Window .TopBar {
        padding: 4px 8px;
        .TopBarInteractionContainer {
            display: flex;
            width: 100vw;
            justify-content: space-between;
            align-items: center;
        }
        .TopBarTitle {width: auto;}
    }

    .settings {
		width: 93vw;
		height: 93%;
		opacity: 0;
		pointer-events: none;
		transition: all ease .3s;
		
        &.active {
            opacity: 1;
            pointer-events: all;
            width: 100vw;
            height: 100%;
        }

        .MaximumExceededBox {
            position: absolute;
            display: flex;
            flex-direction: column;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            border-radius: 5px;
            overflow: hidden;
            z-index: 10000;
            transition: none;
            width: 400px;
            box-shadow: 0px 0 20px #00000089;

            .WindowTopBar {
                background: $bgcolor;
                backdrop-filter: blur(15px);
                -webkit-backdrop-filter: blur(15px);
                padding: 7px 10px;
                display: flex;
                align-items: center;
                transition: all ease .3s;
                border-top-left-radius: 5px;
                border-top-right-radius: 5px;

                .WindowTopBarTitle {
                    color: $fgcolor;
                    font-size: 12px;
                    margin: 0;
                    width: 100%;
                }

                .WindowTopBarInteractionContainer {
                    display: flex;

                    .WindowTopBarInteraction {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-left: 7px;
                        border-radius: 9999px;
                        width: 20px;
                        height: 20px;
                        font-size: 11px;
                        color: $fgcolor;

                        &:hover {
                            background: #3b3b3f;
                        }
        
                        &.close:hover{
                            background: #ff5252;
                        }
                    }
                }
            }

            .WindowBodyDefault {
                background: $bgcolor;
                backdrop-filter: blur(15px);
                -webkit-backdrop-filter: blur(15px);
                border-bottom-left-radius: 5px;
                border-bottom-right-radius: 5px;
                padding: 10px 15px;
                color: $fgcolor;
        
                * {
                    margin: 0;
                }
        
                .WindowBodyTitle {
                    font-size: 17px;
                    margin-bottom: 10px;
                    font-weight: 700;
                }
        
                .WindowBodyContent {
                    font-size: 12px;
                }
            }
      
            .WindowBodyButton {
                display: flex;
                flex-direction: row-reverse;
                margin: 15px 0;
                margin-bottom: 0;
        
                .Button {
                    border: none;
                    outline: none;
                    background-color: #ffffff10;
                    color: $fgcolor;
                    font-size: 12px;
                    padding: 7px 20px;
                    border-radius: 5px;
                    margin-left: 5px;
        
                    &:hover { background-color: #ffffff20; }
                }
            }
        }
        
        .TabBarWrapper {
            margin: 0 10px;
            margin-right: 0;
    
            .TabBar {
                display: flex;
    
                .TabBarItem {
                    padding: 4px 9px;
                    color: #fff;
                    display: flex;
                    align-items: center;
                    font-size: 11px;
                    margin-right: 5px;
    
                    * { margin: 0 }
                    
                    .TabSeparator {
                        background-color: #ffffff1e;
                        width: 1px;
                    }

                    .TabBarInteraction {
                        display: flex;

                        i {
                            margin-right: 3px;
                            font-size: 12px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            border-radius: 999px;
                            height: 20px;
                            width: 20px;
                    	    transition: all ease .3s;

                            &:hover {
                                background-color: #ffffff1e;
                            }

                            &:last-child {
                                margin: 0;
                            }
                        }
                    }
                }

                .TabBarSettingsName {
                    display: flex;
					align-items: center;
					width: 100%;
                    border-radius: 5px;
                }

				.Toggle {
					border-radius: 999px;
					width: 42px;
					height: 20px;
					background: transparentize($fgcolor, 0.9);
					display: flex;
					padding: 0 3px;
					align-items: center;
					transition: all ease .3s;

					&::before {
						content: '';
						display: block;
						background: $fgcolor;
						height: 15px;
						width: 15px;
						border-radius: 999px;
						transition: all ease .1s;
					}

					&.active {
						background: $actcolor;

						&::before {
							transform: translateX(19px);
						}
					}
				}
            }
        }
        
        &.minimize {
            width: 1000px;
            height: 600px;
        }
    }
    
