            .SettingsContainer {
				position: relative;
				width: 100%;
				height: 100%;

				.SettingsContainerInside {
					position: absolute;
					width: 100%;
					height: 100%;
					overflow-y: auto;
					padding: 20px;
					font-size: 13px;
					
					p{margin: 0}

					.WiFiWrapper {
						.WiFi {
							display: flex;
							justify-content: center;
							margin: 0 auto;
		
							.StatusWifiFalse {
								color: transparentize($fgcolor, 0.85);
							}
	
							.VisibleNetworks {
								display: flex;
								flex-direction: column;
								height: 100%;
	
								.VisibleNetworksItem {
									width: 100%;
									padding: 15px 25px;
									display: flex;
									justify-content: space-between;
									border-bottom: 1px solid transparentize($fgcolor, 0.95);
	
									&:hover {
										background: transparentize($fgcolor, 0.97);
									}
	
									&:first-child {
										border-top-left-radius: 7px;
										border-top-right-radius: 7px;
									}
	
									&:last-child {
										border: 0;
										border-bottom-left-radius: 7px;
										border-bottom-right-radius: 7px;
									}
	
									.VisibleNetworksIcon {
										i {
											margin-right: 13px;
	
											&:last-child {margin:0}
	
											&.fa-duotone {--fa-secondary-color: #e2e2e296}
										}
									}
								}
							}
						}
					}

					.AppearanceWrapper {
