								display: flex;
								flex-direction: column;
								
								.ImageContainer {
									border: 3px solid transparent;
									border-radius: 10px;
									img {
										border-radius: 8px;
										width: auto;
										height: 200px;
									}
								}
	
								&[value='1']{
									.ImageContainer.dark {
										transition: border ease .3s;
										border: 3px solid #8fb6ff;
									}
								}
	
								&[value='2']{
