								}
							}

							.ActMenu {
								background: $bgcolor;
								backdrop-filter: blur(15px);
								-webkit-backdrop-filter: blur(15px);
								padding: 5px;
								border-radius: 7px;
								position: absolute;
								box-shadow: 0px 0 20px #0000004e;
								transition: all ease .15s !important;
								opacity: 0;
								pointer-events: none;
							
								&.active {
									opacity: 1;
									pointer-events: all;
								}
								
								.ActMenuSelector {
									padding: 5px 8px;
									border-radius: 7px;
									background: transparent;
									color: $fgcolor;
									font-size: 13px;
									display: flex;
									align-items: center;
							
									* {margin: 0;}
							
									i {margin-right: 7px}
							
									&:hover {
										background: #ffffff12;
									}
								}
							}
						}
					}

					.AboutWrapper {
						.About {
							display: flex;
							justify-content: center;
							margin: 0 auto;
							width: 70%;

							.AboutLogo {margin: 0 auto}

							.AboutMenu {
								width: 600px;
								display: flex;
								justify-content: space-between;
								align-items: center;
								padding: 15px 25px;
								background: transparentize($fgcolor, 0.93);
								border-bottom: 0.5px solid darken($fgcolor, 73%);
	
								&:hover {
									background: transparentize($fgcolor, 0.9);
								}
	
								&:first-child {
									border-top-left-radius: 7px;
									border-top-right-radius: 7px;
								}

								&:last-child {
									border: 0;
									border-bottom-left-radius: 7px;
									border-bottom-right-radius: 7px;
								}

								&.EditName {
									padding: 0 15px;
									display: flex;
									height: 0;
									transition: all ease .3s;
									border: 0!important;
									
									&:hover{background: transparentize($fgcolor, 0.93)}

									*{
										transition: all ease .3s;
									}

									.EditNameInput {
										appearance: none;
										border: none;
										outline: none;
										background: transparentize($fgcolor, 0.9);
										color: inherit;
										width: 100%;
										height: 0;
										padding: 0;
										height: 0;
										font-size: 11px;
									}

									&.active {
										padding: 8px 15px;
										height: 44.5px;

										.EditNameInput {
											padding: 7px 15px;
											height: 28px;
											border-radius: 5px;

											&:focus {
												border-bottom: 1px solid $actcolor;
											}
										}
									}
								}

								.BlurText {color: transparentize($fgcolor, 0.8);}

								i {
									transition: all ease .3s;

									&.rotated {transform:rotate(90deg)}
								}
							}
						}
					}
				}
            }
        }
    }
}

