    .settings {
        .MaximumExceededBox {
            .WindowTopBar {
                background: $fgcolor;

                .WindowTopBarTitle {
                    color: $bgcolor;
                }

                .WindowTopBarInteractionContainer .WindowTopBarInteraction {
                        color: $bgcolor;

                        &:hover {
                            background: #3b3b3f;
                        }
        
                        &.close:hover{
                            background: #ff5252;
                        }
                    }
            }

            .WindowBodyDefault {
                background: $fgcolor;
                color: $bgcolor;
            }
      
            .WindowBodyButton {
                .Button {
                    background-color: #00000010;
